<template>
  <div class="main">
    <div class="list">
      <el-table :data="pageList">        
        <el-table-column prop="name" label="专辑名">
          <template slot-scope="scope">
            <span>
              {{ scope.row.name.replace(/(topic\/)/, '').replace('.jpg', '').replace('.png', '') }}
            </span>
          </template>
        </el-table-column>                   
        <el-table-column prop="lastModified" label="上次更改时间">
          <template slot-scope="scope">
            <span>
              {{ scope.row.lastModified }}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="url" label="封面">
          <template slot-scope="scope">
            <a :href="scope.row.cover" target="_blank">
              <img
                :src="scope.row.url"
                style="width: 150px; height: 111px"
              />
            </a>
          </template>
        </el-table-column>        
        <el-table-column prop="type" label="视频数量">
          <template slot-scope="scope">
            <span>{{ scope.row.type === 'Normal' ? "-" : scope.row.type}}</span>
          </template>          
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleCheckCount(scope.$index, scope.row)"
              >查看视频数量</el-button>
            <!-- <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button> -->
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="block">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page="currentPage"
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="list.length"
      >
      </el-pagination>
    </div>    
  </div>
</template>

<script>
import client from '../oss'
export default {
  name: "topics",
  components: {},
  data() {
    return {      
      list: [],
      currentPage: 1,
      pageSize: 20,
      total: 0,
    };
  },  
  async mounted() {
    try {
      const result = await client.list({
        prefix: 'topic'
      })
      let temp = result.objects      
      temp.shift()
      console.log(temp)
      this.list = temp      
    } catch(err) {
      console.log(err)
    }
  },
  computed: {
    pageList: function () {
      return this.list.slice(
        (this.currentPage - 1) * this.pageSize,
        (this.currentPage - 1) * this.pageSize + this.pageSize
      );
    },
  },
  methods: {
    handleCurrentChange(current) {
      this.currentPage = current;
    },
    handleCheckCount(index, row) {
      row.name = row.name.replace('topic/', '')
      row.name = row.name.slice(0, row.name.lastIndexOf('.'))
      console.log(row.name)
      client.list({
        prefix: 'video/' + row.name
      }).then(res => {     
        console.log(res);   
        let item = this.list[index]
        item.type = res.objects ? res.objects.length : 0
        this.list[index] = item        
      }).catch(() => {
        alert('获取视频数量失败')
      })
    },
    handleEdit(index, row) {        
        this.$router.push({ 
            name: '/topic',
            query: {
              name: row.name,
              lastModified: row.lastModified,
              url: row.url
            }
        })
    },
    async handleDelete(index, row) {
        if (confirm('确认删除此专辑')) {
          try {
            await client.delete(row.name)
            this.list.map((item, index) => {
              if (item.name === row.name) {
                this.list.splice(index, 1)
              }
            })
            alert('删除成功')
          } catch(err) {
            alert('删除失败')
          }
        }
    }
  },
};
</script>

<style scoped>
.main {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.list {
  flex: 1;
  overflow: scroll;
  margin-bottom: 20px;
}

.el-table {
  border-radius: 5px;
}
</style>