<template>
  <div class="main">
    <div class="video-list">
      <el-table :data="pageList">
        <el-table-column prop="name" label="专辑名">
          <template slot-scope="scope">
            <span>{{
              scope.row.name.split("$")[0].replace("video/", "")
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="视频名">
          <template slot-scope="scope">
            <span>{{ scope.row.name.split("$")[1].slice(0, scope.row.name.split("$")[1].lastIndexOf('.')) }}</span>
          </template>
        </el-table-column>        
        <el-table-column prop="lastModified" label="添加时间">
          <template slot-scope="scope">
            <span>{{ scope.row.lastModified }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="url" label="视频">
          <template slot-scope="scope">
            <a :href="'http://ujug.site/mglbox/play.html?name=' + scope.row.url" target="_blank">
              <video :src="scope.row.url" style="width: 150px; height: 111px" />
            </a>
          </template>
        </el-table-column>        
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="block">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page="currentPage"
        :current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import client from "../oss";
export default {
  name: "videos",
  data() {
    return {
      list: [],
      currentPage: 1,
      pageSize: 20,
      total: 0,
    };
  },
  async mounted() {
    const result = await client.list({
      prefix: "video/",
    });
    let total = result.objects;
    if (total) {
      total.shift();
      this.total = total.length;
      this.list = total;
    }
  },
  computed: {
    pageList: function () {
      return this.list.slice(
        (this.currentPage - 1) * this.pageSize,
        (this.currentPage - 1) * this.pageSize + this.pageSize
      );
    },
  },
  methods: {
    handleCurrentChange(current) {
      this.currentPage = current;
    },      
    async handleDelete(index, row) {      
      if (confirm("确认删除此视频")) {
        try {
          let name = row.name          
          await client.delete(name);          
          alert("删除成功");
          this.list.splice(index, 1)
        } catch (err) {
          alert("删除失败");
        }
      }
    },
  },
};
</script>

<style scoped>
.main {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.video-list {
  flex: 1;
  overflow: scroll;
  margin-bottom: 20px;
}

.el-table {
  border-radius: 5px;
}
</style>