<template>
  <div>
      <el-table :data="swiperList">        
        <el-table-column prop="name" label="链接专辑">
          <template slot-scope="scope">
            <span>
              {{ scope.row.name.replace('swiper/', '').replace('.jpg', '').replace('.png', '') }}
            </span>
          </template>
        </el-table-column>    
        <el-table-column prop="lastModified" label="添加时间">
          <template slot-scope="scope">
            <span>
              {{ scope.row.lastModified }}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="url" label="封面">
          <template slot-scope="scope">
            <a :href="scope.row.cover" target="_blank">
              <img
                :src="scope.row.url"
                style="width: 150px; height: 111px"
              />
            </a>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">   
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>      
      <router-link to="/swiper/new">
        <el-button style="margin-top: 20px">添加</el-button>
      </router-link>
  </div>
</template>

<script>
import client from '../oss'
export default {
    name: 'Swiper',
    data() {
        return {
            swiperList: []
        }
    },
    async mounted() {
      try {
        const result = await client.list({
          prefix: 'swiper'
        })
        let temp = result.objects
        temp.shift()
        this.swiperList = temp
      } catch(err) {
        alert('发生错误')
      }
    },
    methods: {
      async handleDelete(index, row) {
        try {
          await client.delete(row.name)
          alert('删除成功')
          this.swiperList.map((item, index) => {
            if (item.name === row.name) {
              this.swiperList.slice(index, 1)
            }
          })
        } catch(err) {
          alert('删除失败')
        }        
      }
    }
}
</script>
