<template>
  <el-main v-loading="loading">
    <el-form :model="form" :rules="rules" label-width="130px">
      <el-form-item label="选择专辑" prop="topic">
        <el-select v-model="form.topic" placeholder="请选择" prop="topic"> 
          <el-option
            v-for="item in options"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="封面图片">
        <el-upload
          class="upload-demo"
          drag
          action=""
          :auto-upload="false"
          :multiple="false"
          :limit="1"
          :on-change="handleUploadChange"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">
              只能上传jpg/png文件，且不超过500kb
            </div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">添加</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </el-main>
</template>

<script>
import client from '../oss'

export default {
  name: 'AddSwiper',
  data() {
    return {            
      loading: false,
      form: {
        topic: "",
        img: "",
      },
      options: [],
      rules: {
        topic: [
          { required: true, message: "请输入专辑名", trigger: "blur" },
          {
            min: 1,
            max: 45,
            message: "长度在 1 到 45 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },  
  mounted() {
    client
      .list({
        prefix: "topic",
      })
      .then((res) => {
        let temp = res.objects;
        temp.shift();
        temp.map((item) => {
          let tempName = item.name.replace("topic/", "");
          tempName = tempName.slice(0, tempName.lastIndexOf("."));
          item.name = tempName;
        });
        this.options = temp;
      })
      .catch(() => {
        alert("出错，请重试");
      });
  },
  methods: {
    async onSubmit() {
      if (/^\s*$/.test(this.form.topic)) {
        alert('请选择跳转的专辑')
        return
      }
      if (!this.form.img) {
        alert('请选择图片')
        return
      }      
      this.loading = true
      try {
        console.log(`/swiper/${this.form.topic}${this.form.img.name.slice(this.form.img.name.lastIndexOf('.'))}`)
        await client.put(`/swiper/${this.form.topic}${this.form.img.name.slice(this.form.img.name.lastIndexOf('.'))}`, this.form.img)
        this.loading = false
        this.$router.push(`/swiper/`);
      } catch(err) {
        this.loading = false
        alert('发生错误')
      }
    },
    handleUploadChange(file) { 
      this.form.img = file.raw
    },
  },
}
</script>

<style scoped>
.el-main {
  flex: none;
  width: 530px;
  margin: 0 auto;
  background: white !important;
  border: 1px solid #dcdfe6;
  border-radius: 5px;
}

.el-select {
  width: 100%;
}
</style>