import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import Topics from '../views/Topics.vue'
import Videos from '../views/Videos.vue'
import AddTopic from '../views/AddTopic.vue'
import AddVideo from '../views/AddVideo.vue'
import Swiper from '../views/SwiperList.vue'
import AddSwiper from '../views/AddSwiper.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: '/',
        component: Home
    },
    {
        path: '/topics',
        name: '/topics',
        component: Topics
    },
    {
        path: '/video/all',
        name: '/video/all',
        component: Videos
    },    
    {
        path: '/topic/new',
        name: '/topic/new',
        component: AddTopic
    },
    {
        path: '/video/new',
        name: '/video/new',
        component: AddVideo
    },    
    {
        path: '/swiper',
        name: '/swiper',
        component: Swiper
    },
    {
      path: '/swiper/new',
      name: '/swiper/new',
      component: AddSwiper
    }    
]

const router = new VueRouter({
    routes
})

export default router