<template>
  <el-main v-loading="loading">
    <el-form :model="form" :rules="rules" label-width="100px">
      <el-form-item label="专辑名" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>      
      <el-form-item label="封面图片">
        <el-upload
          class="upload-demo"
          drag
          action=""
          :auto-upload="false"
          :multiple="false"
          :limit="1"
          :on-change="handleUploadChange"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <!-- <div class="el-upload__tip" slot="tip">
              只能上传jpg/png文件，且不超过500kb
            </div> -->
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">添加</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </el-main>
</template>

<script>
import client from '../oss'

export default {
  name: "addTopic",  
  data() {
    return {            
      loading: false,      
      form: {
        name: "",        
        img: "",
        imgType: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入专辑名", trigger: "blur" },
          {
            min: 1,
            max: 45,
            message: "长度在 1 到 45 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },  
  methods: {
    async onSubmit() {
      this.loading = true
      try {
        await client.put(`/topic/${this.form.name}.${this.form.imgType}`, this.form.img)
        this.loading = false
        alert('添加成功')
        this.$router.push('/topics')
      } catch(err) {
        this.loading = false
        alert('添加失败')        
      }
    },
    handleUploadChange(file) { 
      console.log(file.raw)
      const isJPG = file.raw.type === 'image/jpeg'      
      const isPNG = file.raw.type === 'image/png'
      if (isPNG || isJPG) {
        this.form.img = file.raw
        this.form.imgType = isPNG ? 'png' : 'jpg'
      }      
    },
  },
};
</script>

<style scoped>
.el-main {
  flex: none;
  width: 500px;
  margin: 0 auto;
  background: white !important;
  border: 1px solid #dcdfe6;
  border-radius: 5px;
}
</style>