<template>
  <el-main v-loading="loading">
    <el-form :model="form" :rules="rules" label-width="100px">
      <el-form-item label="视频名" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="选择专辑" prop="topic">
        <el-select v-model="form.topic" placeholder="请选择" prop="topic"> 
          <el-option
            v-for="item in options"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
      </el-form-item>      
      <el-form-item label="视频">
        <el-upload
          class="upload-demo"
          drag
          action=""
          :auto-upload="false"
          :multiple="false"
          :limit="1"
          :on-change="handleUploadChangeVideo"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将视频拖到此处，或<em>点击上传</em></div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </el-main>
</template>

<script>
import client from "../oss";
export default {
  name: "addVideo",
  data() {
    return {
      loading: false,
      form: {
        name: "",
        video: null,        
        topic: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入视频名", trigger: "blur" },
          {
            min: 1,
            max: 45,
            message: "长度在 1 到 45 个字符",
            trigger: "blur",
          },
        ],
        topic: [{ required: true, message: "请选择专辑", trigger: "blur" }],
      },
      options: [],
    };
  },
  mounted() {
    client
      .list({
        prefix: "topic",
      })
      .then((res) => {
        let temp = res.objects;
        temp.shift();
        temp.map((item) => {
          let tempName = item.name.replace("topic/", "");
          tempName = tempName.slice(0, tempName.lastIndexOf("."));
          item.name = tempName;
        });
        this.options = temp;
      })
      .catch(() => {
        alert("出错，请重试");
      });
  },
  methods: {
    async onSubmit() {      
      if (/^\s*$/.test(this.form.name)) {
        alert("请输入视频名");
        return;
      }
      if (/^\s*$/.test(this.form.topic)) {
        alert("请选择专辑");
        return;
      }
      if (!this.form.video) {
        alert("请选择视频");
        return;
      }
      this.loading = true;
      try {        
        const videoPath = `/video/${this.form.topic}$${this.form.name}${this.form.video.name.slice(this.form.video.name.lastIndexOf("."))}`;                
        await client.put(videoPath, this.form.video);
        this.loading = false;
        alert('添加成功')
        this.$router.push('/video/all')
      } catch (err) {
        this.loading = false;
        alert('添加失败')        
      }
    },
    handleUploadChangeImg(file) {
      this.form.img = file.raw;
    },
    handleUploadChangeVideo(file) {
      this.form.video = file.raw;
    },
  },
};
</script>

<style scoped>
.el-main {
  flex: none;
  width: 500px;
  margin: 0 auto;
  background: white !important;
  border: 1px solid #dcdfe6;
  border-radius: 5px;
}

.el-select {
  width: 100%;
}
</style>