<template>
  <div id="app">
    <el-container>
      <el-header>后台管理</el-header>
      <el-container>
        <el-aside width="200px">
          <el-menu
            :default-active="active"
            class="el-menu-vertical-demo"
            @select="handleSelect"            
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
          >
            <el-menu-item index="1">
              <i class="el-icon-menu"></i>
              <span slot="title">首页</span>
            </el-menu-item>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-s-management"></i>
                <span>专辑</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="2-1">所有专辑</el-menu-item>
                <el-menu-item index="2-2" disabled>查找专辑</el-menu-item>
                <el-menu-item index="2-3">添加</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-video-camera"></i>
                <span slot="title">视频</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="3-1">所有视频</el-menu-item>
                <el-menu-item index="3-2" disabled>查找视频</el-menu-item>
                <el-menu-item index="3-3">添加视频</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-menu-item index="4">
              <i class="el-icon-document"></i>
              <span slot="title">首页轮播图</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "app",
  methods: {    
    handleSelect(key) {
      switch (key) {
        case "1":
          this.navigateTo("/");
          break;
        case "2-1":          
          this.navigateTo("/topics");
          break;
        case "2-3":
          this.navigateTo("/topic/new");
          break;
        case "3-1":
          this.navigateTo("/video/all");
          break;
        case "3-3":
          this.navigateTo("/video/new");
          break;
        case "4":
            this.navigateTo("/swiper");
            break;
        default:
          this.navigateTo("/");
          break;
      }
    },
    navigateTo(url) {
      if (this.$route.path !== url) {
        this.$router.push(url);
      }
    },
  },
  data() {
    return {
      active: "1",
    };
  },
  mounted() {
    switch (this.$route.path) {
      case "/topics":
        this.active = "2-1";
        break;
      case "/topic/new":
        this.active = "2-3";
        break;
      case "/video/new":
        this.active = "3-3";
        break;
      case "/video/all":
        this.active = "3-1";
        break;
        case "/swiper":
            this.active = "4"
            break
      case "/":
        this.active = "1";
        break;
      default:
        this.active = "0";
        break;
    }
  },
  watch: {
    $route: {
      handler: function (route) {
        switch (route.path) {
          case "/topics":
            this.active = "2-1";
            break;
          case "/topic/new":
            this.active = "2-3";
            break;
          case "/video/new":
            this.active = "3-3";
            break;
          case "/video/all":
            this.active = "3-1";
            break;
            case "/swiper":
                this.active = "4"
                break
          case "/":
            this.active = "1";
            break;
          default:
            this.active = "0";
            break;
        }
      },
    },
  },
};
</script>

